
.memory-selection-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f0f23 100%);
  z-index: 10000;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* 顶部导航 */
.top-bar {
  padding: 15px 20px;
  display: flex;
  justify-content: center;
}

.step-indicator {
  display: flex;
  align-items: center;
  gap: 10px;

  .step {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
    color: white;

    &.completed {
      background: #4ade80;
    }

    &.active {
      width: auto;
      padding: 8px 20px;
      border-radius: 25px;
      background: linear-gradient(135deg, #a855f7 0%, #7c3aed 100%);
    }
  }

  .step-line {
    width: 30px;
    height: 2px;
    background: rgba(255, 255, 255, 0.3);
  }
}

/* 主要内容区域 */
.main-content {
  flex: 1;
  display: flex;
  padding-top: 20px;
  overflow: hidden;
}

/* 左侧P偶像预览 */
.idol-preview-section {
  width: 40%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.idol-card-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  filter: blur(3px) brightness(0.7);
  transform: scale(1.1);
  z-index: 0;
}

.idol-info-right {
  position: absolute;
  top: 20px;
  right: 20px;
  text-align: right;
  z-index: 2;

  .card-theme {
    font-size: 14px;
    color: #333;
    background: rgba(255, 255, 255, 0.9);
    padding: 4px 12px;
    border-radius: 15px;
    display: inline-block;
    margin-bottom: 8px;
  }

  .idol-name {
    font-size: 28px;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
}

.idol-stats-left {
  position: absolute;
  top: 80px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 2;

  .affection-row {
    background: rgba(255, 255, 255, 0.95);
    padding: 8px 15px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    gap: 10px;

    .label {
      font-size: 12px;
      color: #666;
    }
    .value {
      font-size: 20px;
      font-weight: bold;
      color: #a855f7;
      small {
        font-size: 14px;
        color: #999;
      }
    }
  }
}

.bonus-preview {
  position: absolute;
  bottom: 30px;
  left: 20px;
  background: rgba(20, 20, 40, 0.9);
  border-radius: 15px;
  padding: 15px 20px;
  border: 1px solid rgba(168, 85, 247, 0.5);
  z-index: 2;

  .bonus-title {
    font-size: 14px;
    color: #a855f7;
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
  }

  .bonus-content {
    display: flex;
    align-items: center;
    gap: 10px;

    .label {
      font-size: 12px;
      color: #888;
    }

    .bonus-value {
      font-size: 16px;
      font-weight: bold;
      color: white;
    }
  }
}

/* 右侧回忆卡区域 */
.memory-section {
  flex: 1;
  padding: 20px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* 预设导航 */
.preset-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
}

.preset-arrow {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(168, 85, 247, 0.3);
  border: 2px solid rgba(168, 85, 247, 0.6);
  color: white;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background: rgba(168, 85, 247, 0.5);
    transform: scale(1.1);
  }
}

.preset-info {
  text-align: center;
  min-width: 200px;
}

.preset-name-display {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  transition: all 0.2s;

  span {
    font-size: 18px;
    font-weight: bold;
    color: white;
  }

  i {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.6);
  }

  &:hover {
    background: rgba(255, 255, 255, 0.2);
  }
}

.preset-name-input {
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid #a855f7;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  text-align: center;
  width: 200px;
  outline: none;
}

.preset-index {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  margin-top: 8px;
}

.memory-slots {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  max-width: 100%;
  margin: 0 auto;
}

.memory-slot {
  aspect-ratio: 3/4;
  background: rgba(255, 255, 255, 0.05);
  border: 2px dashed rgba(255, 255, 255, 0.3);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;

  &:hover {
    border-color: rgba(168, 85, 247, 0.8);
    background: rgba(168, 85, 247, 0.1);
  }

  &.filled {
    border-style: solid;
    border-color: #a855f7;
    background: rgba(168, 85, 247, 0.2);
  }

  &.selected {
    border-color: #a855f7;
    box-shadow: 0 0 20px rgba(168, 85, 247, 0.5);
  }

  .slot-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 13px;
  }

  .slot-name {
    position: absolute;
    bottom: 30px;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 12px;
    color: white;
    background: rgba(0, 0, 0, 0.7);
    padding: 5px;
  }

  .slot-stars {
    position: absolute;
    bottom: 8px;
    color: gold;
    font-size: 12px;
  }

  .empty-slot {
    font-size: 40px;
    color: rgba(255, 255, 255, 0.3);
  }
}

.formation-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 30px;

  .save-btn {
    padding: 10px 25px;
    background: linear-gradient(135deg, #a855f7 0%, #7c3aed 100%);
    border: none;
    color: white;
    border-radius: 25px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: all 0.3s;

    &:hover {
      box-shadow: 0 4px 15px rgba(168, 85, 247, 0.5);
    }
  }

  .reset-btn,
  .auto-btn {
    padding: 10px 25px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    background: rgba(255, 255, 255, 0.1);
    color: white;
    border-radius: 25px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.2s;

    &:hover {
      background: rgba(255, 255, 255, 0.2);
    }
  }
}

/* 底部按钮 */
.bottom-actions {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.3);

  .action-btn {
    padding: 15px 40px;
    border: none;
    border-radius: 30px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s;

    &.back-btn {
      background: rgba(255, 255, 255, 0.2);
      color: white;

      &:hover {
        background: rgba(255, 255, 255, 0.3);
      }
    }

    &.next-btn {
      background: linear-gradient(135deg, #a855f7 0%, #7c3aed 100%);
      color: white;

      &:hover {
        box-shadow: 0 4px 20px rgba(168, 85, 247, 0.5);
      }
    }
  }
}

/* 关闭按钮 */
.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
  z-index: 100;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
  }
}

/* 卡片选择弹窗 */
.card-picker-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-picker-content {
  width: 90%;
  max-width: 800px;
  max-height: 85vh;
  background: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.picker-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  background: linear-gradient(135deg, #a855f7 0%, #7c3aed 100%);
  color: white;

  h3 {
    margin: 0;
    font-size: 20px;
  }

  .picker-close {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.2);
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;

    &:hover {
      background: rgba(255, 255, 255, 0.3);
    }
  }
}

.card-detail-area {
  display: flex;
  padding: 20px;
  gap: 20px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-bottom: 1px solid #dee2e6;
  min-height: 150px;
}

.detail-left {
  flex: 1;

  .detail-header {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
  }

  .detail-character {
    font-size: 14px;
    color: #666;
    margin-bottom: 15px;
  }

  .skill-cards {
    .skill-label {
      font-size: 12px;
      color: #a855f7;
      font-weight: bold;
      margin-bottom: 5px;
    }
    .skill-list {
      font-size: 14px;
      color: #333;
    }
  }
}

.detail-right {
  flex: 1;

  .effects-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .effect-item {
    background: white;
    padding: 10px 15px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    color: #333;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.08);

    i {
      color: #a855f7;
    }
  }
}

.card-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
  padding: 15px;
  overflow-y: auto;
  max-height: 280px;
  background: #f8f9fa;
}

.picker-card {
  position: relative;
  aspect-ratio: 3/4;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.2s;
  border: 3px solid transparent;

  &:hover {
    transform: scale(1.05);
  }

  &.preview {
    border-color: #a855f7;
    box-shadow: 0 0 15px rgba(168, 85, 247, 0.5);
  }

  &.selected {
    opacity: 0.6;
  }

  .picker-card-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .picker-card-name {
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    font-size: 10px;
    padding: 3px 5px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .picker-card-stars {
    position: absolute;
    bottom: 3px;
    right: 5px;
    color: gold;
    font-size: 10px;
  }

  .picker-selected-badge {
    position: absolute;
    top: 5px;
    right: 5px;
    background: #a855f7;
    color: white;
    font-size: 10px;
    padding: 2px 6px;
    border-radius: 5px;
  }
}

.no-cards-message {
  grid-column: 1 / -1;
  text-align: center;
  color: #666;
  padding: 30px;
}

.picker-actions {
  display: flex;
  gap: 15px;
  padding: 15px 20px;
  background: #f8f9fa;
  border-top: 1px solid #e9ecef;

  .picker-btn {
    flex: 1;
    padding: 15px;
    border: none;
    border-radius: 12px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: all 0.2s;

    &.cancel {
      background: #e9ecef;
      color: #666;

      &:hover {
        background: #dee2e6;
      }
    }

    &.confirm {
      background: linear-gradient(135deg, #a855f7 0%, #7c3aed 100%);
      color: white;

      &:hover {
        box-shadow: 0 4px 15px rgba(168, 85, 247, 0.4);
      }

      &:disabled {
        background: #ccc;
        cursor: not-allowed;
      }
    }
  }
}
