
.character-select-page {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f0f23 100%);
  z-index: 1000;
}

/* 左侧面板 */
.left-panel {
  width: 35%;
  min-width: 300px;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.4);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.panel-header {
  padding: 20px;
  background: linear-gradient(135deg, rgba(255, 107, 157, 0.2) 0%, rgba(147, 39, 143, 0.2) 100%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  h2 {
    margin: 0;
    color: white;
    font-size: 1.2rem;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 10px;

    i {
      color: #ff6b9d;
    }
  }
}

.character-list {
  flex: 1;
  overflow-y: auto;
  padding: 10px;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 107, 157, 0.3);
    border-radius: 3px;
  }
}

.character-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 15px;
  margin-bottom: 8px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateX(5px);
  }

  &.active {
    background: linear-gradient(135deg, rgba(255, 107, 157, 0.3) 0%, rgba(147, 39, 143, 0.3) 100%);
    border-color: #ff6b9d;
  }
}

.char-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid;
  flex-shrink: 0;
  overflow: hidden;

  .avatar-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.char-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.char-name {
  color: white;
  font-size: 1rem;
  font-weight: 600;
}

.char-unit {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

.active-icon {
  color: #ff6b9d;
  font-size: 1.2rem;
}

/* 卡面选择 */
.card-select {
  padding: 15px;
  background: rgba(0, 0, 0, 0.3);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  max-height: 300px;
  overflow-y: auto;

  /* 自定义滚动条 */
  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(102, 126, 234, 0.5);
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: rgba(102, 126, 234, 0.7);
  }
}

.section-title {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 8px;

  i {
    color: #667eea;
  }
}

.card-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.dress-type-header {
  font-size: 0.75rem;
  font-weight: 600;
  color: #667eea;
  padding: 8px 5px 4px;
  margin-top: 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);

  &:first-child {
    margin-top: 0;
    border-top: none;
  }
}

.animation-selector {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: 20px;

  label {
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #667eea;
    }
  }

  .animation-select {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    padding: 6px 12px;
    color: white;
    font-size: 0.85rem;
    cursor: pointer;
    min-width: 120px;

    &:focus {
      outline: none;
      border-color: #667eea;
    }

    option {
      background: #1a1a2e;
      color: white;
    }
  }
}

.card-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
  }

  &.active {
    background: linear-gradient(135deg, rgba(102, 126, 234, 0.3) 0%, rgba(118, 75, 162, 0.3) 100%);
    border: 1px solid rgba(102, 126, 234, 0.5);
  }

  &.locked {
    opacity: 0.5;
    cursor: not-allowed;
    background: rgba(100, 100, 100, 0.2);

    &:hover {
      background: rgba(100, 100, 100, 0.2);
    }

    .card-name {
      color: rgba(255, 255, 255, 0.5);
    }
  }

  .lock-icon {
    color: #888;
    font-size: 0.8rem;
    margin-right: 8px;
  }

  .card-name {
    color: white;
    font-size: 0.9rem;
  }

  i {
    color: #667eea;
  }
}

/* 右侧面板 */
.right-panel {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
  position: relative;
}

.spine-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 15px;
}

.spine-name {
  color: white;
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.costume-toggle {
  display: flex;
  gap: 10px;
}

.costume-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid transparent;
  border-radius: 25px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover:not(:disabled) {
    background: rgba(255, 255, 255, 0.15);
    color: white;
  }

  &.active {
    background: linear-gradient(135deg, rgba(147, 39, 143, 0.8) 0%, rgba(70, 39, 133, 0.8) 100%);
    border-color: rgba(255, 255, 255, 0.3);
    color: white;
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }

  i {
    font-size: 1rem;
  }
}

.spine-preview-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  overflow: hidden;
  position: relative;
}

.spine-preview {
  width: 100%;
  height: 100%;
}

.no-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  color: rgba(255, 255, 255, 0.3);

  i {
    font-size: 5rem;
  }

  p {
    font-size: 1.2rem;
    margin: 0;
  }
}

/* 操作按钮 */
.action-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  margin-top: 20px;
}

.cancel-btn,
.apply-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 30px;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;

  i {
    font-size: 1rem;
  }
}

.cancel-btn {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.7);
  border: 2px solid rgba(255, 255, 255, 0.2);

  &:hover {
    background: rgba(255, 255, 255, 0.15);
    color: white;
    border-color: rgba(255, 255, 255, 0.3);
  }
}

.apply-btn {
  background: linear-gradient(135deg, #ff6b9d 0%, #c44aca 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 107, 157, 0.4);

  &:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 107, 157, 0.5);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

/* 响应式 */
@media (max-width: 768px) {
  .character-select-page {
    flex-direction: column;
  }

  .left-panel {
    width: 100%;
    max-width: none;
    height: 40%;
  }

  .right-panel {
    height: 60%;
  }
}
