
.resource-bar {
  display: flex;
  gap: 20px;
  padding: 15px 25px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.resource-item {
  display: flex;
  align-items: center;
  gap: 8px;
  color: white;
  font-weight: bold;

  .icon {
    font-size: 20px;
  }

  .icon-img {
    width: 36px;
    height: 36px;
    object-fit: contain;
    background: transparent;
    border: none;
    filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.3));
  }

  .label {
    font-size: 14px;
    opacity: 0.9;
  }

  .value {
    font-size: 20px;
    min-width: 60px;
    text-align: right;
  }

  &.gems .value {
    color: #ffd700;
  }

  &.stardust .value {
    color: #87ceeb;
  }
}
