
.produce-select-page {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f0f23 100%);
  z-index: 1000;
}

/* 左侧舞台预览 */
.stage-preview {
  flex: 1;
  position: relative;
  overflow: hidden;
}

.stage-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stage-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 30px;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));
}

.stage-name {
  color: white;
  font-size: 2rem;
  margin: 0 0 10px 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.stage-desc {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1rem;
  margin: 0;
}

/* 右侧面板 */
.scenario-panel {
  width: 450px;
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
}

.panel-header {
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.produce-type-toggle {
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 4px;
}

.type-btn {
  flex: 1;
  padding: 10px 15px;
  border: none;
  background: transparent;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;

  &.active {
    background: linear-gradient(135deg, #ff6b9d 0%, #c44aca 100%);
    color: white;
  }

  &:hover:not(.active) {
    color: white;
  }
}

/* 副本列表 */
.scenario-list {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.scenario-card {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s;
  border: 3px solid transparent;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  }

  &.selected {
    border-color: #ff6b9d;
    box-shadow: 0 0 20px rgba(255, 107, 157, 0.4);
  }
}

.scenario-banner {
  width: 100%;
  height: auto;
  display: block;
}

.scenario-info {
  position: absolute;
  top: 10px;
  left: 10px;
}

.bonus-label {
  background: linear-gradient(135deg, #ff4757 0%, #ff6b9d 100%);
  color: white;
  padding: 5px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: bold;
}

/* 副本说明 */
.scenario-detail {
  padding: 15px 20px;
  background: rgba(255, 255, 255, 0.05);
  border-top: 1px solid rgba(255, 255, 255, 0.1);

  p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    line-height: 1.6;
    margin: 0;
  }
}

/* 底部按钮 */
.action-buttons {
  display: flex;
  gap: 15px;
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.back-btn,
.start-btn {
  flex: 1;
  padding: 15px;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s;
}

.back-btn {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.2);

  &:hover {
    background: rgba(255, 255, 255, 0.2);
  }
}

.start-btn {
  background: linear-gradient(135deg, #ff6b9d 0%, #c44aca 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 107, 157, 0.4);

  &:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 107, 157, 0.5);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
